body {
  height: 100vh;
  background-color: pink;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#scoreBoard, #choiceDisplay {
  flex-flow: row;
  justify-content: space-evenly;
  display: flex;
}

a {
  color: #fff;
  text-align: center;
  padding: 14px 25px;
  text-decoration: none;
  display: inline-block;
}

a.yes {
  background-color: green;
}

a.no {
  background-color: red;
}

a:hover {
  cursor: pointer;
  background-color: #789;
}

.score {
  margin: 2vw;
  font-weight: bolder;
}

.space {
  width: 10vw;
}

.hidden {
  display: none;
}

#highscore-container {
  width: -moz-fit-content;
  width: fit-content;
  border: 2px solid #000;
  flex-direction: column;
  align-self: start;
  padding: 1em;
  display: flex;
  position: fixed;
}

